button {
    margin: var(--spacing-md);
    width: 150px;
    height: 50px;
    border: none;
    border-radius: 5px;
    font-family: cabin;
    color: var(--white);
    font-weight: bold;
    background-color: var(--blue);
    transition: all 0.5s;
    cursor: pointer;
}

button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

button span:after {
    content: "\00bb";
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

button:hover span {
    padding-right: 25px;
}

button:hover span:after {
    opacity: 1;
    right: 0;
}
